body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html, body{
  width: 100%;
  height: 100%;
}

body{
  overflow: hidden;
}


#root{
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000000;
  color: #eaeaea;
}

header{
  box-sizing: border-box;
  padding: 21px;
}

header h1{
  font-family: "Source Serif Pro", serif;
  font-size: 55px;
  line-height: 55px;
  margin: 0;
}


header p{
  padding-left: 55px;
}

.menu{
  box-sizing: border-box;
  padding: 16px;
  width: 100%;
}

.menu-container{
  position: relative;
  left: 0px;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-gap: 8px;
  justify-content: center;
}

@media screen and (max-width: 992px) {
  .menu-container {
    grid-template-columns: 50% 50%;
  }
}

@media screen and (max-width: 500px) {
  .menu-container {
    grid-template-columns: 100%;
  }
}

.menu-container>a{
  box-sizing: border-box;
  display: block;
  border: 1px solid #212121;
}



.menu-container>a>img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.play{
  display: inline-block;
  position: fixed;
  top:0;
  left:0;
}

.play.full{
  width: 100%;
  height: 100%;
  background-color: #1d1d1d;
}


.play .play-container{
  width: 100%;
  height: 100%;
  position: relative;
}

.play .play-container a.back,
.play .play-container a.info{
  position: absolute;
  color: #fff;
  top: 21px;
  right: 34px;
  font-size: 34px;
}

.play .play-container a:active{
  opacity:0.5;
}

.play .play-container a.info{
  top: 21px;
  right: 89px;
}
